// =====================================================
// Footer Styles Partial
// =====================================================


.footer {

    display: grid;
    justify-content: left;
    padding: 0px 15px;
    background-color: $footer-background-color;
    color: $white;

    letter-spacing: 0.4px;

}


ul {
    margin: 0px 0px;
    border: 0px 0px;
    padding-inline-start: 0px;
}

.footer__logo{

    width: 320px;
    height: auto;

    padding-top: 27px;


}

h4 {
    font-weight: 400;
}

li {
    font-size: 13px;
    line-height: 22px;
}

a{
    text-decoration: none;
    color: $footer-text-color;

}


a:hover {
    //color:#00A0C6; 
    //text-decoration:none; 
    //cursor:pointer;
    color: red;  
}

.footer__copyright{
    background-color: $footer-bottom-color;
}